.logotype {
  font-family: "Kumar One", cursive;
  text-transform: lowercase !important; }

.cv__container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 0;
  text-align: center;
  width: 100%;
  background-image: -webkit-radial-gradient(circle, #fffcf9 50%, #e6dbd1 100%);
  background-image: radial-gradient(circle, #fffcf9 50%, #e6dbd1 100%);
  font-family: Helvetica, Arial, sans-serif; }
  .cv__container h1 {
    font-family: "Kumar One", cursive;
    text-transform: lowercase;
    font-size: 4.8rem;
    color: #5B5B5B; }
  .cv__container h2 {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 1.6rem;
    color: #5B5B5B; }
  .cv__container h3, .cv__container h4 {
    color: #5B5B5B; }
  .cv__container p {
    font-family: "Noto Serif", serif;
    color: #5B5B5B; }
  .cv__container li {
    font-family: "Noto Serif", serif;
    color: #5B5B5B; }
  .cv__container a {
    text-decoration: none;
    color: #5B5B5B;
    font-weight: bold; }

.cv__section {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 100%; }
  .cv__section h1 {
    width: 100%;
    font-size: 2rem;
    font-family: "Noto Serif", serif;
    font-weight: normal;
    letter-spacing: 3px;
    margin-bottom: 0;
    text-transform: uppercase; }

.cv__title {
  width: 100%;
  word-break: break-word;
  font-size: 4.4rem; }

.cv__item {
  margin: 20px; }
  .cv__item h2 {
    margin-top: 0; }
  .cv__item ul {
    padding: 0;
    margin-bottom: 0; }
  .cv__item li {
    list-style: none;
    margin-bottom: 5px; }

.cv__divider {
  border: none;
  border-bottom: 1px dotted black;
  margin: 20px 0;
  width: 80%; }

.cv__section.work .cv__item ul, .cv__section.education .cv__item ul, .cv__section.awards .cv__item ul {
  margin-top: 0; }

.cv__section.work .cv__item li, .cv__section.education .cv__item li, .cv__section.awards .cv__item li {
  margin-bottom: 20px; }
  .cv__section.work .cv__item li li, .cv__section.education .cv__item li li, .cv__section.awards .cv__item li li {
    margin-bottom: 0; }

